.examples {
  background: white;
}

.carousel-container {
  margin-bottom: 80px;
}

.carousel {
  position: relative;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  background: white;
}

.carousel-track {
  display: flex;
  transition: transform 0.5s ease;
  width: 100%;
}

.carousel-slide {
  min-width: 100%;
  flex-shrink: 0;
  width: 100%;
}

.slide-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  min-height: 500px;
  width: 100%;
}

.slide-image {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.example-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(10px);
}

.image-placeholder.small {
  height: 200px;
  border-radius: 12px;
}

.placeholder-content {
  text-align: center;
  color: white;
}

.placeholder-content span {
  display: block;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 10px;
}

.placeholder-content small {
  font-size: 0.9rem;
  opacity: 0.8;
}

.slide-info {
  padding: 60px 40px;
  background: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.slide-info h3 {
  color: #1e3a8a;
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.slide-info p {
  color: #64748b;
  line-height: 1.7;
  margin-bottom: 40px;
  font-size: 1.1rem;
}

.slide-stats {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
}

.slide-stats .stat {
  text-align: center;
  padding: 20px;
  background: #f8fafc;
  border-radius: 12px;
  border: 2px solid #e2e8f0;
}

.stat-value {
  display: block;
  font-size: 1.5rem;
  font-weight: 700;
  color: #1e3a8a;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 0.9rem;
  color: #64748b;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(255, 255, 255, 0.9);
  border: none;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-size: 1.5rem;
  color: #1e3a8a;
  cursor: pointer;
  transition: all 0.3s ease;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
}

.carousel-btn:hover {
  background: white;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
  transform: translateY(-50%) scale(1.1);
}

.carousel-btn.prev {
  left: 20px;
}

.carousel-btn.next {
  right: 20px;
}

.carousel-dots {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 30px;
}

.dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  border: none;
  background: #e2e8f0;
  cursor: pointer;
  transition: all 0.3s ease;
}

.dot.active {
  background: #1e3a8a;
  transform: scale(1.2);
}

.examples-gallery {
  margin-top: 60px;
}

.examples-gallery h3 {
  color: #1e3a8a;
  font-size: 1.8rem;
  font-weight: 600;
  text-align: center;
  margin-bottom: 40px;
}

.gallery-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
}

.gallery-item {
  background: white;
  border-radius: 16px;
  overflow: hidden;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.gallery-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.15);
}

.gallery-image {
  height: 200px;
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  display: flex;
  align-items: center;
  justify-content: center;
}

.gallery-thumb {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.gallery-caption {
  padding: 20px;
}

.gallery-caption h4 {
  color: #1e3a8a;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 10px;
}

.gallery-caption p {
  color: #64748b;
  font-size: 0.9rem;
  margin: 0;
}

.advantages-list {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-top: 20px;
}

.advantage-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 15px;
  background: #f8fafc;
  border-radius: 12px;
  border: 2px solid #e2e8f0;
  transition: all 0.3s ease;
}

.advantage-item:hover {
  background: #f1f5f9;
  border-color: #1e3a8a;
  transform: translateX(5px);
}

.advantage-icon {
  color: #10b981;
  font-weight: bold;
  font-size: 1.2rem;
  flex-shrink: 0;
  margin-top: 2px;
}

.advantage-text {
  color: #1e3a8a;
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 500;
}

@media (max-width: 768px) {
  .slide-content {
    grid-template-columns: 1fr;
    min-height: auto;
  }
  
  .slide-info {
    padding: 40px 20px;
  }
  
  .slide-info h3 {
    font-size: 1.5rem;
  }
  
  .slide-stats {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .carousel-btn {
    width: 40px;
    height: 40px;
    font-size: 1.2rem;
  }
  
  .gallery-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    gap: 20px;
  }
} 