.contact {
  background: #f8fafc;
}

.contact-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 60px;
  margin-bottom: 60px;
}

.contact-info {
  display: flex;
  flex-direction: column;
  gap: 30px;
}

.info-card {
  display: flex;
  align-items: center;
  gap: 20px;
  background: white;
  padding: 30px;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  transition: transform 0.3s ease;
}

.info-card:hover {
  transform: translateY(-5px);
}

.info-icon {
  width: 60px;
  height: 60px;
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
  color: white;
}

.info-content h3 {
  color: #1e3a8a;
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 8px;
}

.info-content p {
  color: #333;
  font-size: 1.1rem;
  font-weight: 500;
  margin-bottom: 5px;
}

.info-content small {
  color: #64748b;
  font-size: 0.9rem;
}

.contact-form {
  background: white;
  padding: 40px;
  border-radius: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: #64748b;
  font-weight: 500;
  font-size: 0.9rem;
}

.form-group input,
.form-group textarea {
  padding: 15px;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
  font-family: inherit;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #1e3a8a;
}

.form-group textarea {
  resize: vertical;
  min-height: 120px;
}

.submit-btn {
  width: 100%;
  margin-top: 20px;
  padding: 18px;
  font-size: 1.1rem;
}

.submit-btn.loading {
  opacity: 0.7;
  cursor: not-allowed;
}

.success-message {
  background: #d1fae5;
  color: #065f46;
  padding: 15px;
  border-radius: 8px;
  margin-top: 20px;
  border: 1px solid #10b981;
  text-align: center;
  font-weight: 500;
}

.error-message {
  background: #fee2e2;
  color: #991b1b;
  padding: 15px;
  border-radius: 8px;
  margin-top: 20px;
  border: 1px solid #ef4444;
  text-align: center;
  font-weight: 500;
}

.contact-map {
  margin-top: 60px;
}

.map-placeholder {
  width: 100%;
  height: 400px;
  background: linear-gradient(135deg, #e2e8f0 0%, #cbd5e1 100%);
  border-radius: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #94a3b8;
  position: relative;
  overflow: hidden;
}

.map-placeholder::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(45deg, transparent 30%, rgba(255, 255, 255, 0.3) 50%, transparent 70%);
  animation: shimmer 2s infinite;
}

.map-content {
  text-align: center;
  color: #64748b;
  z-index: 1;
}

.map-content span {
  display: block;
  font-size: 1.2rem;
  font-weight: 500;
  margin-bottom: 10px;
}

.map-content small {
  font-size: 0.9rem;
  opacity: 0.7;
}

@media (max-width: 768px) {
  .contact-content {
    grid-template-columns: 1fr;
    gap: 40px;
  }
  
  .form-row {
    grid-template-columns: 1fr;
    gap: 15px;
  }
  
  .contact-form {
    padding: 30px 20px;
  }
  
  .info-card {
    padding: 20px;
  }
  
  .map-placeholder {
    height: 300px;
  }
} 