.advantages {
  background: white;
}

.advantages-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-bottom: 80px;
}

.advantage-card {
  background: #f8fafc;
  padding: 40px 30px;
  border-radius: 20px;
  text-align: center;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  position: relative;
  overflow: hidden;
}

.advantage-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #1e3a8a, #3b82f6);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.advantage-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(30, 58, 138, 0.15);
  border-color: #e2e8f0;
}

.advantage-card:hover::before {
  transform: scaleX(1);
}

.advantage-icon {
  width: 80px;
  height: 80px;
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 25px;
  font-size: 2.5rem;
  color: white;
  box-shadow: 0 10px 25px rgba(30, 58, 138, 0.3);
}

.advantage-card h3 {
  color: #1e3a8a;
  font-size: 1.4rem;
  font-weight: 600;
  margin-bottom: 15px;
}

.advantage-card p {
  color: #64748b;
  line-height: 1.7;
  margin-bottom: 20px;
}

.advantage-feature {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  display: inline-block;
}

.advantages-highlight {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  border-radius: 24px;
  padding: 60px;
  color: white;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.advantages-highlight::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="dots" width="30" height="30" patternUnits="userSpaceOnUse"><circle cx="15" cy="15" r="2" fill="rgba(255,255,255,0.1)"/></pattern></defs><rect width="100%" height="100%" fill="url(%23dots)"/></svg>');
  z-index: 1;
}

.highlight-content {
  position: relative;
  z-index: 2;
}

.highlight-content h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
}

.highlight-content p {
  font-size: 1.1rem;
  line-height: 1.7;
  margin-bottom: 40px;
  opacity: 0.9;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.highlight-stats {
  display: flex;
  justify-content: center;
  gap: 60px;
  flex-wrap: wrap;
}

.highlight-stat {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.highlight-stat .stat-number {
  font-size: 2.5rem;
  font-weight: 700;
  color: #fbbf24;
}

.highlight-stat .stat-label {
  font-size: 0.9rem;
  opacity: 0.8;
  text-transform: uppercase;
  letter-spacing: 1px;
}

@media (max-width: 768px) {
  .advantages-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .advantage-card {
    padding: 30px 20px;
  }
  
  .advantages-highlight {
    padding: 40px 20px;
  }
  
  .highlight-content h3 {
    font-size: 1.5rem;
  }
  
  .highlight-content p {
    font-size: 1rem;
  }
  
  .highlight-stats {
    gap: 30px;
  }
  
  .highlight-stat .stat-number {
    font-size: 2rem;
  }
} 