.pricing {
  background: #f8fafc;
}

.pricing-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  margin-bottom: 60px;
}

.pricing-card {
  background: white;
  border-radius: 20px;
  padding: 40px 30px;
  text-align: center;
  position: relative;
  transition: all 0.3s ease;
  border: 2px solid transparent;
  cursor: pointer;
}

.pricing-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
}

.pricing-card.selected {
  border-color: #1e3a8a;
  box-shadow: 0 20px 40px rgba(30, 58, 138, 0.2);
}

.pricing-card.popular {
  border-color: #fbbf24;
  transform: scale(1.05);
}

.popular-badge {
  position: absolute;
  top: -15px;
  left: 50%;
  transform: translateX(-50%);
  background: linear-gradient(135deg, #fbbf24 0%, #f59e0b 100%);
  color: white;
  padding: 8px 20px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 600;
}

.pricing-header {
  margin-bottom: 30px;
}

.pricing-header h3 {
  color: #1e3a8a;
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 20px;
}

.price {
  display: flex;
  align-items: baseline;
  justify-content: center;
  gap: 5px;
}

.currency {
  font-size: 1.2rem;
  color: #64748b;
  font-weight: 500;
}

.amount {
  font-size: 3rem;
  font-weight: 700;
  color: #1e3a8a;
}

.period {
  font-size: 1rem;
  color: #64748b;
  font-weight: 500;
}

.pricing-features {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 30px;
}

.pricing-features li {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 0;
  color: #64748b;
  border-bottom: 1px solid #e2e8f0;
}

.pricing-features li:last-child {
  border-bottom: none;
}

.check-icon {
  color: #10b981;
  font-weight: bold;
  font-size: 1.1rem;
}

.pricing-btn {
  width: 100%;
  margin-top: 20px;
}

.pricing-note {
  margin-bottom: 60px;
}

.note-card {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  color: white;
  padding: 30px;
  border-radius: 16px;
  text-align: center;
}

.note-card h4 {
  font-size: 1.3rem;
  margin-bottom: 20px;
}

.note-card ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.note-card li {
  padding: 8px 0;
  opacity: 0.9;
}

.pricing-calculator {
  background: white;
  padding: 40px;
  border-radius: 16px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
}

.pricing-calculator h3 {
  color: #1e3a8a;
  font-size: 1.5rem;
  margin-bottom: 30px;
  text-align: center;
}

.calculator-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  align-items: end;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: #64748b;
  font-weight: 500;
  font-size: 0.9rem;
}

.hours-input,
.plan-select {
  padding: 12px 16px;
  border: 2px solid #e2e8f0;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.hours-input:focus,
.plan-select:focus {
  outline: none;
  border-color: #1e3a8a;
}

.total-price {
  background: #f8fafc;
  padding: 20px;
  border-radius: 8px;
  text-align: center;
  font-size: 1.2rem;
  color: #1e3a8a;
  font-weight: 600;
}

.price-description {
  color: #64748b;
  font-size: 0.9rem;
  margin-top: 10px;
  text-align: center;
}

.pricing-highlight {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  border-radius: 24px;
  padding: 60px;
  color: white;
  text-align: center;
  margin-bottom: 60px;
  position: relative;
  overflow: hidden;
}

.pricing-highlight::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="dots" width="30" height="30" patternUnits="userSpaceOnUse"><circle cx="15" cy="15" r="2" fill="rgba(255,255,255,0.1)"/></pattern></defs><rect width="100%" height="100%" fill="url(%23dots)"/></svg>');
  z-index: 1;
}

.pricing-highlight .highlight-content {
  position: relative;
  z-index: 2;
}

.pricing-highlight h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 40px;
}

.highlight-features {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 30px;
  text-align: left;
}

.highlight-feature {
  display: flex;
  align-items: flex-start;
  gap: 15px;
}

.feature-icon {
  font-size: 2rem;
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 12px;
  flex-shrink: 0;
}

.highlight-feature h4 {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 8px;
  color: #fbbf24;
}

.highlight-feature p {
  font-size: 0.95rem;
  opacity: 0.9;
  line-height: 1.5;
  margin: 0;
}

.price-highlight {
  background: linear-gradient(135deg, #1e3a8a 0%, #3b82f6 100%);
  border-radius: 20px;
  padding: 40px;
  text-align: center;
  margin-bottom: 60px;
  color: white;
  position: relative;
  overflow: hidden;
}

.price-highlight::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1000 1000"><defs><pattern id="stars" width="50" height="50" patternUnits="userSpaceOnUse"><circle cx="25" cy="25" r="1" fill="rgba(255,255,255,0.2)"/></pattern></defs><rect width="100%" height="100%" fill="url(%23stars)"/></svg>');
  z-index: 1;
}

.price-highlight > * {
  position: relative;
  z-index: 2;
}

.price-badge {
  display: inline-flex;
  align-items: baseline;
  gap: 5px;
  background: rgba(255, 255, 255, 0.2);
  padding: 15px 25px;
  border-radius: 50px;
  margin-bottom: 20px;
  backdrop-filter: blur(10px);
}

.price-main {
  font-size: 3rem;
  font-weight: 700;
  color: white;
}

.price-label {
  font-size: 1.2rem;
  font-weight: 500;
  color: white;
}

.price-highlight h3 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 10px;
  color: white;
}

.price-highlight p {
  font-size: 1.1rem;
  opacity: 0.9;
  margin: 0;
}

@media (max-width: 768px) {
  .pricing-grid {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .pricing-card {
    padding: 30px 20px;
  }
  
  .pricing-card.popular {
    transform: none;
  }
  
  .amount {
    font-size: 2.5rem;
  }
  
  .calculator-form {
    grid-template-columns: 1fr;
  }
  
  .pricing-calculator {
    padding: 30px 20px;
  }
} 